@import url("normalize.css");

@font-face {
	font-family: "IBM ISO 9";
	src: url(../public/Web437_IBM_DOS_ISO9.woff) format("woff");
	font-weight: normal;
	font-style: normal;
}

@mixin for-phone-only {
	@media (max-width: 599px) {
		@content;
	}
}
@mixin for-tablet-portrait-up {
	@media (min-width: 600px) {
		@content;
	}
}
@mixin for-tablet-landscape-up {
	@media (min-width: 900px) {
		@content;
	}
}
@mixin for-desktop-up {
	@media (min-width: 1200px) {
		@content;
	}
}
@mixin for-big-desktop-up {
	@media (min-width: 1800px) {
		@content;
	}
}

* {
	image-rendering: pixelated;
	font-family: "IBM ISO 9", monospace;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

// ***
// rules that position app on the screen + app background
// ***
#app {
	min-height: 100vh;
	min-width: 100vw;
	background-color: black;
	background-image: url("../public/zhitel-small.jpg");
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;

	color: rgb(148, 230, 255);
	overflow: scroll;
	display: flex;
	flex-direction: column;
	justify-content: center;

	.synth {
		height: 100%;
	}

	// ***
	// dialog that prompts user to rotate screen if on mobile
	// ***
	#rotateDeviceDialog {
		@include for-tablet-portrait-up {
			@media screen and (orientation: landscape) {
				display: none;
			}
		}

		position: fixed;
		z-index: 2;
		top: 0;
		height: 100%;
		width: 100%;

		background-color: transparent;
		backdrop-filter: grayscale(100%) blur(4px);
		border: none;

		.dialog-contents {
			max-width: 600px;
			height: 100%;
			margin: 0 auto;
			font-size: 1.5rem;
			display: flex;
			flex-direction: column;
			justify-content: center;
		}

		p {
			color: white;
			padding: 1rem;
			text-align: center;
			background: linear-gradient(rgb(0, 50, 124), black);
		}

		form {
			text-align: center;
		}

		button {
			color: white;
			background: linear-gradient(rgb(0, 50, 124), black);
			padding: 0rem 2rem 0rem 2rem;
			border: none;
			font-size: 1.5rem;
			margin-top: 1rem;

			&:active {
				color: red;
			}

			@media (hover: hover) {
				&:hover {
					color: red;
				}
			}
		}
	}

	.synth {
		width: 100%;
		max-width: 600px;
		margin: 0 auto;

		h1 {
			background: linear-gradient(rgb(0, 50, 124), black);
			text-transform: uppercase;
			font-size: 2rem;
			width: 100%;
			margin-bottom: 1rem;
			padding: 0 0.5rem 0 0.5rem;
		}

		// ***
		// contaier for synth controls
		// ***
		.modules {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			gap: 10px;

			// ***
			// each column with sliders
			// ***
			.controls {
				position: relative;
				display: flex;
				flex-direction: column;
				// justify-content: flex-start;
				// flex-basis: 0;
				// flex-grow: 1;
				// flex-shrink: 1;

				font-size: 1rem;

				h2 {
					text-transform: capitalize;
					background: linear-gradient(rgb(0, 21, 52), black);
					margin-bottom: 0.5rem;
					width: 100%;
				}

				.control-el {
					height: 75px;
					margin-bottom: 0.5rem;

					label {
						background-color: black;
						background: linear-gradient(rgb(0, 21, 52), black);
						font-size: 1.2rem;
						padding: 2px;
						display: block;
						width: 100%;
						margin-bottom: 0.5rem;
					}

					select {
						background-color: black;
						background: linear-gradient(rgb(0, 21, 52), black);
						color: rgb(148, 230, 255);

						font-size: 1.2rem;
						border: none;
					}

					// ***
					// REMOVING DEFAULT SLIDER STYLES
					// ***
					input[type="range"] {
						-webkit-appearance: none; /* Hides the slider so that custom slider can be made */
						appearance: none;
						width: 100%; /* Specific width is required for Firefox. */
						background: transparent; /* Otherwise white in Chrome */
						padding: 0rem 0 1rem 0;
					}
					// ***
					// STYLING THE THUMB
					// ***
					input[type="range"]::-webkit-slider-thumb {
						-webkit-appearance: none;
					}
					input[type="range"]:focus {
						outline: none; /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
					}
					input[type="range"]::-ms-track {
						width: 100%;
						cursor: pointer;
						/* Hides the slider so custom styles can be added */
						background: transparent;
						border-color: transparent;
						color: transparent;
					}

					/* Special styling for WebKit/Blink */
					input[type="range"]::-webkit-slider-thumb {
						-webkit-appearance: none;
						border: 4px solid rgb(148, 230, 255);
						height: 22px;
						width: 22px;
						border-radius: 50%;
						background: transparent;
						cursor: pointer;
						margin-top: -6px; /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
						// box-shadow: 2px 2px 0px #000000; /* Add cool effects to your sliders! */
					}

					/* All the same stuff for Firefox */
					input[type="range"]::-moz-range-thumb {
						border: 4px solid rgb(148, 230, 255);
						height: 18px;
						width: 18px;
						border-radius: 50%;
						background: transparent;
						// box-shadow: 2px 2px 0px #000000;
						cursor: pointer;
					}

					// ***
					// STYLING THE TRACK
					// ***
					input[type="range"]::-webkit-slider-runnable-track {
						width: 100%;
						height: 8.4px;
						cursor: pointer;
						box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
						background: linear-gradient(rgb(0, 21, 52), black);
						// border-radius: 1px;
						// border: 6px solid white;
					}

					// input[type="range"]:focus::-webkit-slider-runnable-track {
					// 	background: #367ebd;
					// }

					input[type="range"]::-moz-range-track {
						width: 100%;
						height: 8.4px;
						cursor: pointer;
						box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
						background: linear-gradient(rgb(0, 21, 52), black);
						border-radius: 1.3px;
						border: 0.2px solid #010101;
					}
				}
			}
		}
	}

	// ***
	// cat image that appears sometimes when you play
	// ***
	.shanin {
		position: absolute;
		transform: translate(-40%, 60%);
		background-image: url("../public/shamin-smaller.png");
		background-size: 100%;
		aspect-ratio: 1/1;
		width: 250px;
	}

