	// ***
	// self-explanatory
	// ***

	#keyboard {
		height: 500px;
		position: absolute;
		margin: 0 auto;
		// background-color: aliceblue;

		// this is one key
		ul {
			position: absolute;
			transform: translateY(-5px);
		}

		.octave-control {
			position: absolute;
			top: 50%;
			right: 50%;
			transform: translate(calc(250px + 60%), -50%);
			font-size: 1.5rem;

			#octave-up {
				padding: 10px;
				transform: translateY(calc(-5% - 5px));
				background: linear-gradient(to bottom, rgb(0, 50, 124), black);
				&.active {
					color: white;
					background: linear-gradient(to bottom, red, black);
				}
				&:active {
					color: white;
					background: linear-gradient(to bottom, red, black);
				}
			}

			#octave-down {
				padding: 10px;
				transform: translateY(calc(-5%));
				background: linear-gradient(to top, rgb(0, 50, 124), black);

				&.active {
					color: white;
					background: linear-gradient(to bottom, red, black);
				}
				&:active {
					color: white;
					background: linear-gradient(to bottom, red, black);
				}
			}
		}
	}
}
